/* NORMALIZE CSS */

@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;

0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
* {
  box-sizing: border-box;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  margin: 0;
}

body {
  margin: 0;
}

main {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

img {
  border-style: none;
}

button,
input,
optgroup,
select,
textarea {
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details {
  display: block;
}

summary {
  display: list-item;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

@font-face {
  font-family: "poppins";
  src: url("../fonts/Poppins-Regular.ttf");
}

@font-face {
  font-family: "inter-medium";
  src: url("../fonts/Inter-Medium.ttf");
}

@font-face {
  font-family: "inter-bold";
  src: url("../fonts/Inter-Bold.ttf");
}

nav {
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.3019607843);
}

nav .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 16px;
}

nav .container .nav__logo h2 a {
  color: #000000;
  text-decoration: none;
  font-size: 24px;
  font-family: "inter-bold";
}

nav .container .nav__menu {
  display: flex;
  gap: 48px;
}

nav .container .nav__menu .nav__menu__item a {
  position: relative;
  text-decoration: none;
  color: #000000;
  font-size: 16px;
}

nav .container .nav__menu .nav__menu__item a::before {
  content: "";
  width: 0%;
  height: 1px;
  background: rgba(0, 0, 0, 0.5019607843);
  position: absolute;
  left: 0;
  bottom: -5px;
  transition: 0.3s;
}

nav .container .nav__menu .nav__menu__item a:hover::before {
  width: 100%;
}

nav .container .nav__menu form {
  display: none;
}

nav .container .nav__icons {
  display: flex;
  align-items: center;
  gap: 24px;
}

nav .container .nav__icons form {
  position: relative;
}

nav .container .nav__icons form input {
  background: #F5F5F5;
  border-radius: 4px;
  padding: 7px 45px 7px 20px;
  border: none;
  outline: 0;
  width: 243px;
  height: 38px;
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5019607843);
}

nav .container .nav__icons form input::placeholder {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5019607843);
}

nav .container .nav__icons form button {
  position: absolute;
  right: 12px;
  top: 8px;
  border: none;
  background: none;
  cursor: pointer;
}

nav .container .nav__icons .nav__icons__end {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 16px;
}

nav .container .nav__icons .nav__icons__end .to__wish__list__btn a,
nav .container .nav__icons .nav__icons__end .to__basket__btn a {
  display: block;
}

nav .container .nav__icons .nav__icons__end .to__wish__list__btn a svg,
nav .container .nav__icons .nav__icons__end .to__basket__btn a svg {
  display: block;
  width: 100%;
  height: 100%;
}

nav .container .nav__icons .nav__icons__end .burger__menu {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  width: 30px;
  height: 20px;
}

nav .container .nav__icons .nav__icons__end .burger__menu span {
  display: block;
  background: #000000;
  width: 100%;
  height: 2px;
  border-radius: 4px;
  transition: 0.3s;
}

nav .container .nav__icons .nav__icons__end .burger__menu.active span:nth-child(1) {
  transform: rotate(-45deg) translate(-11px, 2px);
}

nav .container .nav__icons .nav__icons__end .burger__menu.active span:nth-child(2) {
  display: none;
}

nav .container .nav__icons .nav__icons__end .burger__menu.active span:nth-child(3) {
  transform: rotate(45deg) translate(-11px, -2px);
}

header {
  margin-top: 40px;
}

header .container {
  display: flex;
  gap: 64px;
}

header .container .header__menu {
  position: relative;
}

header .container .header__menu .header__menu__item {
  display: flex;
  justify-content: space-between;
  width: 217px;
  height: 24px;
  margin-bottom: 16px;
}

header .container .header__menu .header__menu__item a {
  font-size: 16px;
  color: #000000;
  text-decoration: none;
  white-space: nowrap;
  width: 100%;
  line-height: 24px;
}

header .container .header__menu .header__menu__item .header__menu__item__open {
  cursor: pointer;
  transition: transform 0.3s;
}

header .container .header__menu .header__menu__item:last-child {
  margin-bottom: 0px;
}

header .container .header__menu::after {
  content: "";
  width: 0.5px;
  height: calc(100% + 40px);
  position: absolute;
  background: #000000;
  top: -40px;
  right: 0px;
  opacity: 0.3;
}

header .container .header__slider {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 24px;
  width: 892px;
  background: #000;
  padding: 32px;
}

header .container .header__slider .header__slider__content {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
}

header .container .header__slider .header__slider__content .header__slider__left {
  flex-basis: 294px;
  white-space: nowrap;
}

header .container .header__slider .header__slider__content .header__slider__left .header__slider__brand {
  display: flex;
  align-items: center;
  gap: 16px;
}

header .container .header__slider .header__slider__content .header__slider__left .header__slider__brand img {
  display: block;
  margin: 0;
}

header .container .header__slider .header__slider__content .header__slider__left .header__slider__brand span {
  font-size: min(16px, max(8px + 2vw));
  text-align: center;
  color: #FAFAFA;
}

header .container .header__slider .header__slider__content .header__slider__left .header__slider__title h4 {
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: min(48px, max(24px + 2vw));
  letter-spacing: 2px;
  color: #FAFAFA;
  margin: 24px 0px;
}

header .container .header__slider .header__slider__content .header__slider__left .header__slider__btn a {
  display: flex;
  align-items: center;
  gap: 12px;
  font-size: min(16px, max(8px + 2vw));
  text-decoration: none;
  color: #FAFAFA;
}

header .container .header__slider .header__slider__content .header__slider__left .header__slider__btn a span {
  border-bottom: 1px solid #FAFAFA;
  padding-bottom: 4px;
}

header .container .header__slider .header__slider__content .header__slider__right {
  flex-basis: 400px;
}

header .container .header__slider .header__slider__content .header__slider__right img {
  display: block;
  width: 100%;
}

header .container .header__slider .header__slider__dots {
  display: flex;
  justify-content: center;
  gap: 12px;
}

header .container .header__slider .header__slider__dots .dot {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fff;
  opacity: 0.5;
  cursor: pointer;
}

header .container .header__slider .header__slider__dots .dot.active__dot {
  background: #DB4444;
  opacity: 1;
  border: 2px solid #fff;
}

