main {
  margin-top: 140px;
  margin-bottom: 140px;
}

main .container .items_template_section {
  border-bottom: 0.5px solid rgba(0, 0, 0, 0.1882352941);
  margin-bottom: 70px;
  padding-bottom: 70px;
}

main .container .items_template_section:nth-child(5) {
  border-bottom: none;
}

main .container .items_template_section .its_category_title {
  color: #DB4444;
  font-size: 16px;
  font-weight: 600;
  position: relative;
  padding-left: 32px;
  line-height: 40px;
  margin: 0px 0px 24px 0px;
}

main .container .items_template_section .its_category_title::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 20px;
  height: 40px;
  border-radius: 4px;
  background: #DB4444;
}

main .container .items_template_section .its_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

main .container .items_template_section .its_header .itsh_left {
  display: flex;
  align-items: center;
  gap: 87px;
}

main .container .items_template_section .its_header .itsh_left .its__header__title {
  font-family: "Inter", sans-serif;
  font-weight: 600;
  font-size: min(36px, max(18px + 2vw));
  margin: 0;
}

main .container .items_template_section .its_header .itsh_left .timer {
  display: flex;
  align-items: center;
  gap: 17px;
}

main .container .items_template_section .its_header .itsh_left .timer h5 {
  margin: 0;
}

main .container .items_template_section .its_header .itsh_left .timer h5 span {
  display: block;
}

main .container .items_template_section .its_header .itsh_left .timer h5 span:first-child {
  font-weight: 500;
  color: #000000;
  margin-bottom: 4px;
  font-size: 12px;
}

main .container .items_template_section .its_header .itsh_left .timer h5 span:last-child {
  font-size: min(32px, max(16px + 2vw));
  font-family: "Inter", sans-serif;
  font-weight: 700;
}

main .container .items_template_section .its_header .itsh_left .timer .seperator {
  margin-top: 12px;
}

main .container .items_template_section .its_header .itsh_left .timer .seperator div {
  width: 4px;
  height: 4px;
  background: #DB4444;
  border-radius: 50% 50%;
  margin-bottom: 8px;
}

main .container .items_template_section .its_header .itsh_left .timer .seperator div:last-child {
  margin-bottom: unset;
}

main .container .items_template_section .its_header .itsh_right {
  display: flex;
  gap: 8px;
}

main .container .items_template_section .its_header .itsh_right .left__btn,
main .container .items_template_section .its_header .itsh_right .right__btn {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F5F5F5;
  width: 46px;
  height: 46px;
  border-radius: 50% 50%;
  cursor: pointer;
}

main .container .items_template_section .its_header .itsh_right .left__btn svg,
main .container .items_template_section .its_header .itsh_right .right__btn svg {
  width: 20px;
  height: 20px;
}

main .container .items_template_section .its_header .itsh_right .left__btn svg path,
main .container .items_template_section .its_header .itsh_right .right__btn svg path {
  transition: fill 0.3s;
  fill: #000000;
}

main .container .items_template_section .its_header .itsh_right .left__btn:first-child svg,
main .container .items_template_section .its_header .itsh_right .right__btn:first-child svg {
  transform: rotate(180deg);
}

main .container .items_template_section .its_header .itsh_right .left__btn:hover svg path,
main .container .items_template_section .its_header .itsh_right .right__btn:hover svg path {
  fill: #DB4444;
}

main .container .items_template_section .its_items {
  display: flex;
  gap: 30px;
  margin-top: 40px;
}

main .container .items_template_section .its_items .its_item {
