}

main .container .items_template_section .its_items .its_item .its_item_header {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #F5F5F5;
  padding: 12px;
  border-radius: 4px;
  position: relative;
  max-width: 270px;
  max-height: 250px;
  min-width: 270px;
  min-height: 250px;
  overflow: hidden;
}

main .container .items_template_section .its_items .its_item .its_item_header .sale {
  position: absolute;
  top: 12px;
  left: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  font-size: 12px;
  background: #DB4444;
  color: #FFFFFF;
  padding: 4px 12px;
  border-radius: 4px;
  user-select: none;
}

main .container .items_template_section .its_items .its_item .its_item_header .sales {
  position: absolute;
  top: 12px;
  left: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: fit-content;
  font-size: 12px;
  background: #00FF66;
  color: #FFFFFF;
  padding: 4px 12px;
