  align-items: center;
  width: 34px;
  height: 34px;
  border-radius: 50% 50%;
  background: #FFFFFF;
  cursor: pointer;
}

main .container .items_template_section .its_items .its_item .its_item_header .wishlist {
  position: absolute;
  top: 12px;
  right: 12px;
}

main .container .items_template_section .its_items .its_item .its_item_header .preview {
  position: absolute;
  top: 54px;
  right: 12px;
}

main .container .items_template_section .its_items .its_item .its_item_header .image {
  max-width: 172px;
  max-height: 152px;
  overflow: hidden;
}

main .container .items_template_section .its_items .its_item .its_item_header .image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

main .container .items_template_section .its_items .its_item .its_item_header .to__basket {
  position: absolute;
  bottom: -100px;
  left: 0;
  width: 100%;
  padding: 8px 16px;
  text-align: center;
  background: #000000;
  color: #FFFFFF;
  cursor: pointer;
  transition: all 0.3s;
}

main .container .items_template_section .its_items .its_item .its_item_header .to__basket:hover {
  background: #00FF66;
  color: #000000;
}

main .container .items_template_section .its_items .its_item .its_item_title {
  font-weight: 500;
  margin: 16px 0px 0px 0px;
}

main .container .items_template_section .its_items .its_item .its_item_title a {
  text-decoration: none;
  color: #000000;
  font-size: 16px;
}

main .container .items_template_section .its_items .its_item .its_item_price {
  display: flex;
  align-items: center;
  gap: 12px;
  margin: 8px 0px;
}

main .container .items_template_section .its_items .its_item .its_item_price .current_price,
main .container .items_template_section .its_items .its_item .its_item_price .old_price {
  margin: 0;
}

main .container .items_template_section .its_items .its_item .its_item_price .curren_price {
  color: #DB4444;
}

main .container .items_template_section .its_items .its_item .its_item_price .old_price {
  color: #000000;
  opacity: 0.5;
  text-decoration: line-through;
}

main .container .items_template_section .its_items .its_item .its_item_review {
  display: flex;
  align-items: center;
  gap: 8px;
}

main .container .items_template_section .its_items .its_item .its_item_review .comments_count p {
  font-size: 14px;
  color: #000000;
  opacity: 0.5;
  font-weight: 600;
  margin: 0;
}

main .container .items_template_section .its_items .its_item:hover .its_item_header .to__basket {
  bottom: 0px;
}

main .container .items_template_section .its_categories {
  display: flex;
  gap: 30px;
  margin-top: 60px;
}

main .container .items_template_section .its_categories .category a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 170px;
  height: 145px;
  border: 1px solid rgba(0, 0, 0, 0.3019607843);
  border-radius: 4px;
  text-decoration: none;
  transition: all 0.3s;
}

main .container .items_template_section .its_categories .category a svg {
  margin-bottom: 16px;
}

main .container .items_template_section .its_categories .category a svg path,
main .container .items_template_section .its_categories .category a svg line {
  transition: inherit;
}

main .container .items_template_section .its_categories .category a span {
  font-size: 16px;
  color: #000000;
  transition: inherit;
}

